import React, { Component } from "react";
import styled from "styled-components";

const Div = styled.section`
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.1), inset 0 0 0 1px rgba(0, 0, 0, 0.1);
  margin-top: 35px;
  transition: all 350ms;
`;

const A_Tag = styled.a`
  display: flex;
  text-decoration: none;
  @media screen and (max-width: 767px) {
    flex-wrap: wrap;
  }
`;

const Text = styled.div`
  width: 80%;
  padding: 16px;
  order: 1;
  position: relative;
  .source-title {
    color: black;
    span {
      border: 1px solid gold;
      color: gold;
      border-radius: 5px;
      font-size: 13px;
      padding: 1px 4px;
    }
  }
  .source-words,
  .source-website {
    color: rgba(0, 0, 0, 0.5);
    font-size: 13px;
  }
  .source-website {
    position: absolute;
    bottom: 16px;
    margin: 0;
  }
  @media screen and (max-width: 767px) {
    width: 100%;
    order: 2;
  }
`;
const Image = styled.div`
  width: 20%;
  padding-bottom: 20%;
  background: url("/static/img/chow.PNG") no-repeat center;
  background-size: cover;
  border-left: 1px solid #e0e0e0;
  order: 2;
  @media screen and (max-width: 767px) {
    width: 100%;
    padding-bottom: 50%;
    order: 1;
  }
`;

class Source extends Component {
  render() {
    return (
      <Div>
        <A_Tag href="https://www.google.com.tw" target="_blank">
          <Text>
            <h3 className="source-title">
              「周而復始 生生不息」 揉合傳統與現代之產品
              <span>廣告</span>
            </h3>
            <p className="source-words">
              以稀有的寶石搭配恆久不變的貴金屬
              盡心盡意製作而成的精美首飾，古往今來、不論中西方，皆是人們對深愛的人表達愛意、獻上祝福的最佳禮物。點睛品
              (周生生集團) 珠寶，秉承傳統的心意，結合最先進的科技，
              打造出設計獨到、匠心獨運的首飾，誠意為您提供最優質精緻的產品，讓您傳達真摯的祝福和愛意。
            </p>
            <p className="source-website">www.interaction-design.org</p>
          </Text>
          <Image />
        </A_Tag>
      </Div>
    );
  }
}

export default Source;
